
#sideNav {
  width: 180px;
  height: 100vh;
  position: fixed;
  left: 0px;
  top: 20;
  background: #F4B41A;
  z-index: 2;
  transition: .5s;
  padding-left: 20px;
}

nav ul li{
  list-style: none;
  margin: 60px 10px;
}
nav ul li .sidebuttons{
  text-decoration: none;
  color: #143D59;
  font-weight: 600;
  font-size: 20px;
}


@media screen and (max-width: 768px) {
  #sideNav {
    margin: 1px 1px;
    padding-left: 3px;
    width: 100px;
  }

  nav ul li .sidebuttons{
    text-decoration: none;
    color: #fff;
    font-size: 10px;
  }
  
}